/* eslint-disable max-classes-per-file */
/* eslint-disable react/no-multi-comp */

import React, {useEffect, useState} from 'react'
import {
  Button,
  Container,
  Divider,
  Grid,
  Header,
  Icon,
  Menu,
  Dropdown,
  Message,
} from 'semantic-ui-react'
import {interpret, standard_output, Parser} from "viet-ngu";
import MainEditor from './MainEditor';
import { introduction, variables, operators, functions,
    controlFlow, loops, arrays, classes
} from './lessons/';


const options = [
    { key: 'Bt u', text: 'Bt u', value: 'Bt u' },
    { key: 'Khai bo',  text: 'Khai bo', value: 'Khai bo' },
    { key: 'Php Ton',  text: 'Php Ton', value: 'Php ton' },
    { key: 'Hm',  text: 'Hm', value: 'Hm' },
    { key: 'R Nhnh', text: 'R Nhnh', value: 'R Nhnh' },
    { key: 'Vng Lp', text: 'Vng Lp', value: 'Vng Lp' },
    { key: 'Mng', text: 'Mng', value: 'Mng' },
    { key: 'Lp', text: 'Lp', value: 'Lp' },
  ]

const MessageContent = ({output}) => {
    return (
        <Message.Content>
            {output.map((each) => <p>{each}</p>)}
        </Message.Content>
    )
}

const HomepageLayout = () => {

    const [ source, setSource ] = useState("");
    const [ output, setOutput] = useState([]);
    const [ lesson, setLesson] = useState("chao ban");
    const parser = new Parser();

    useEffect(() => {
       setSource(introduction);
    }, [introduction]);


    const play = () => {
        //reset standard_output
        standard_output.splice(0, standard_output.length);
        setOutput([...standard_output]);
        interpret(source);
        setOutput([...standard_output]);

    }

    const stop = () => {
        setSource("");
        standard_output.splice(0, standard_output.length);
        setOutput([...standard_output]);
    }

    const format = () => {
        const result = parser
            .setSource(source)
            .generateTokens()
            .transformTokensValue()
            .returnAllTokensAsText();

        setSource(result);
    }

    const selectLesson = (e, {value}) => {
        switch(value){
            case 'Bt u': {
                setSource(introduction);
                setLesson('Bt u');
                break;
            }
            case 'Khai bo': {
                setSource(variables);
                setLesson('Khai bo');
                break;
            }
            case 'Php ton': {
                setSource(operators);
                setLesson('Php ton');
                break;
            }
            case 'Hm': {
                setSource(functions);
                setLesson('Hm');
                break;
            }
            case 'R Nhnh' : {
                setSource(controlFlow);
                setLesson('R Nhnh');
                break;
            }
            case 'Vng Lp': {
                setSource(loops);
                setLesson('Vng Lp');
                break;
            }
            case 'Mng': {
                setSource(arrays);
                setLesson('Mng');
                break;
            }
            case 'Lp': {
                setSource(classes);
                setLesson('Lp');
                break;
            }
            default:
                break;// unreachable
        }
    }


    return (
        <Container>
            <Menu
                fixed={ 'top' }
                color='yellow'
                inverted
                size='large'
            >
            </Menu>
            <Divider hidden />
            <Divider hidden />
            <Divider hidden />
            <Grid className="editor_toolbar">
                <Grid.Column floated='left' width={5}>
                    <Header as='h1' color='blue'>
                        Nguyn Mu
                    </Header>
                </Grid.Column>

                <Grid.Column floated='right' width={8} >
                    <Button inverted color='green' compact onClick={format}>
                        <Icon name='edit' />nh dng
                    </Button>
                    <Button inverted color='green' compact onClick={play}>
                        <Icon name='play' /> Chy 
                    </Button>

                    <Button inverted color='red' compact onClick={stop}>
                        <Icon name='stop' />qut trng
                    </Button>

                    <Button.Group inverted >
                        <Button compact >Bi vit: {lesson}</Button>
                        <Dropdown
                            className='button icon'
                            floating
                            options={options}
                            onChange={selectLesson}
                            trigger={<></>}
                            />
                    </Button.Group>
                </Grid.Column>
            </Grid>

            <Divider hidden />
        <MainEditor setSource={setSource} value={source}/>
            
    <Message warning attached='bottom' size='massive' className="output_console">
        <Message.Header>Console Output:</Message.Header>
        <MessageContent output={output} />
        </Message>

    </Container>
    )
    }

export default HomepageLayout