import TokenType from "./Compile/Types/TokenType.js";

export default class Scanner {
    source = '';
    current = 0;
    start = 0;
    line = 1;

    setSource(source){
        this.source = source;
    }

    isAtEnd() {
        return this.current >= this.source.length;
    };

    makeToken(type, payload){
        let token = {
            length: this.current - this.start,
            line: this.line,
            type,
            payload

        };
        return token;
    };

    errorToken (message) {
        let token = {
            type: TokenType.TOKEN_ERROR,
            payload:  message,
            length:  message.length,
            line:  this.line,
        };

        return token;
    };

    advance () {
        return this.source.charAt(this.current++);
    };

    match (expected) {
        if (this.isAtEnd()) return false;
        if (this.source.charAt(this.current) !== expected) return false;
        this.current++;
        return true;
    };

    peek(){
        //look at the current character, but does not move the current forward
        return this.source.charAt(this.current);
    };

    peekNext (){
        if (this.isAtEnd()) return '\0';
        return this.source.charAt(this.current + 1);
    };

    skipWhiteSpace () {
        //keep skipping empty string, carriage return, tab,
        //newlines, and comments.
        for(;;){
            const character = this.peek();
            switch(character){
                case ' ':
                case '\r':
                case '\t':
                    this.advance();
                    break;
                case '\n': {
                    this.line++;
                    this.advance();
                    break;
                }
                //need to escape slash
                case '/': {
                    if (this.peekNext() === '/') {
                        //A comment goes until the end of the line
                        while(this.peek() !== '\n' && !this.isAtEnd()) {
                            this.advance();
                        }
                    } else {
                        return;
                    }
                    break;
                }
                default:
                    return;
            }
        }
    };

    checkKeyword (keyword, type){
        //take the word from scanner
        const word = this.source.substring(this.current - keyword.length, this.current);
        const keyWordLength = keyword.length;
        const wordLength = this.current - this.start;
        //compare they have the same length and content
        if ( (wordLength === keyWordLength) && (word === keyword)){
            return  type;
        }

        return TokenType.TOKEN_IDENTIFIER;
    };

    //TODO redo how scanner lex identifier and keywords
    vietIdentifierType(){
        const currentCharacter = this.source.charAt(this.start);
        switch(currentCharacter){
            case 'b': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.substring(this.start + 1, this.start + 3);
                    switch (innerCharacter){
                        case 'ie': return this.checkKeyword('bien', TokenType.TOKEN_VAR);
                        case 'i': return this.checkKeyword('bin', TokenType.TOKEN_VAR);
                    }
                }
                break;
            }
            case 'c': return this.checkKeyword('cho', TokenType.TOKEN_FOR);
            case 'd': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.charAt(this.start + 1);
                    switch (innerCharacter){
                        case 'o': return this.checkKeyword('doi', TokenType.TOKEN_SWITCH);
                        case 'u': return this.checkKeyword('dung', TokenType.TOKEN_TRUE);
                    }
                }
                break;
            }
            case '': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.charAt(this.start + 1);
                    switch (innerCharacter){
                        case '': return this.checkKeyword('i', TokenType.TOKEN_SWITCH);
                        case '': return this.checkKeyword('ng', TokenType.TOKEN_TRUE);
                    }
                }
                break;
            }
            case 'g': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.charAt(this.start + 1);
                    switch (innerCharacter){
                        case '': return this.checkKeyword('gc', TokenType.TOKEN_SUPER);
                        case 'o': return this.checkKeyword('goc', TokenType.TOKEN_SUPER);
                    }
                }
                break;
            }
            case 'h': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.substring(this.start + 1, this.start + 3);
                    switch (innerCharacter){
                        case 'am': return this.checkKeyword('ham', TokenType.TOKEN_FUN);
                        case 'oa': return this.checkKeyword('hoac', TokenType.TOKEN_OR);
                        case 'm': return this.checkKeyword('hm', TokenType.TOKEN_FUN);
                        case 'o': return this.checkKeyword('hoc', TokenType.TOKEN_OR);
                    }
                }
                break;
            }
            case 'k': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.substring(this.start + 1, this.start + 4);//3 characters
                    switch (innerCharacter){
                        case 'hac': return this.checkKeyword('khac', TokenType.TOKEN_ELSE);
                        case 'hie': return this.checkKeyword('khiem_dien', TokenType.TOKEN_DEFAULT);
                        case 'hc': return this.checkKeyword('khc', TokenType.TOKEN_ELSE);
                        case 'hi': return this.checkKeyword('khim_din', TokenType.TOKEN_DEFAULT);
                    }
                }
                break;
            }
            case 'l': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.charAt(this.start + 1);
                    switch (innerCharacter){
                        case 'o': return this.checkKeyword('lop', TokenType.TOKEN_CLASS);
                        case '': return this.checkKeyword('lp', TokenType.TOKEN_CLASS);
                    }
                }
                break;
            }
            case 'n': {
                //TODO potential bug need fix with 3 char and a space
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.substring(this.start + 1, this.start + 4);//3 char
                    switch (innerCharacter){
                        case 'ay ': return this.checkKeyword('nay', TokenType.TOKEN_THIS);
                        case 'eu ': return this.checkKeyword('neu', TokenType.TOKEN_IF);
                        case 'ghi': return this.checkKeyword('nghi', TokenType.TOKEN_BREAK);
                        case 'hap': return this.checkKeyword('nhap', TokenType.TOKEN_IMPORT);
                        case 'y ': return this.checkKeyword('ny', TokenType.TOKEN_THIS);
                        case 'u ': return this.checkKeyword('nu', TokenType.TOKEN_IF);
                        case 'gh': return this.checkKeyword('ngh', TokenType.TOKEN_BREAK);
                        case 'hp': return this.checkKeyword('nhp', TokenType.TOKEN_IMPORT);
                    }
                }
                break;
            }
            case 's': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.charAt(this.start + 1);
                    switch (innerCharacter){
                        case 'a': return this.checkKeyword('sai', TokenType.TOKEN_FALSE);
                    }
                }
                break;
            }
            case 't': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.substring(this.start + 1, this.start + 3);
                    switch (innerCharacter){
                        case 'he': return this.checkKeyword('theo', TokenType.TOKEN_EXTENDS);
                        case 'ie': return this.checkKeyword('tiep', TokenType.TOKEN_CONTINUE);
                        case 'ra': return this.checkKeyword('tra', TokenType.TOKEN_RETURN);
                        case 'ro': return this.checkKeyword('trong_khi', TokenType.TOKEN_WHILE);
                        case 'ru': return this.checkKeyword('truong_hop', TokenType.TOKEN_CASE);
                        case 'u_': return this.checkKeyword('tu_day', TokenType.TOKEN_FROM);
                        case 'i': return this.checkKeyword('tip', TokenType.TOKEN_CONTINUE);
                        case 'r': return this.checkKeyword('tr', TokenType.TOKEN_RETURN);
                        case 'r': return this.checkKeyword('trng_khi', TokenType.TOKEN_WHILE);
                        case 'r': return this.checkKeyword('trng_hp', TokenType.TOKEN_CASE);
                        case '_': return this.checkKeyword('t_y', TokenType.TOKEN_FROM);
                    }
                }
                break;
            }
            case 'v': {
                //TODO need fix, potential bug with space char
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.substring(this.start + 1, this.start + 3);
                    switch (innerCharacter){
                        case 'a ': return this.checkKeyword('va', TokenType.TOKEN_AND);
                        case 'ie': return this.checkKeyword('viet', TokenType.TOKEN_PRINT);
                        case 'o_': return this.checkKeyword('vo_gia', TokenType.TOKEN_NIL);
                        case ' ': return this.checkKeyword('v', TokenType.TOKEN_AND);
                        case 'i': return this.checkKeyword('vit', TokenType.TOKEN_PRINT);
                        case '_': return this.checkKeyword('v_gi', TokenType.TOKEN_NIL);

                    }
                }
                break;
            }
            case 'x': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.substring(this.start + 1, this.start + 3);
                    switch (innerCharacter){
                        case 'ua': return this.checkKeyword('xuat', TokenType.TOKEN_EXPORT);
                        case 'u': return this.checkKeyword('xut', TokenType.TOKEN_EXPORT);
                    }
                }
                break;
            }
        }
        return TokenType.TOKEN_IDENTIFIER;
    }

    identifierType(){
        const currentCharacter = this.source.charAt(this.start);
        switch(currentCharacter){
            case 'a': return this.checkKeyword('and', TokenType.TOKEN_AND);
            case 'b': return this.checkKeyword('break', TokenType.TOKEN_BREAK);
            case 'c': if (this.current - this.start > 1) {
                const innerCharacter = this.source.charAt(this.start + 1);
                switch (innerCharacter) {
                    case 'a': return this.checkKeyword('case', TokenType.TOKEN_CASE);
                    case 'l':return this.checkKeyword('class', TokenType.TOKEN_CLASS);
                    case 'o': return this.checkKeyword('continue', TokenType.TOKEN_CONTINUE);
                }
            }
            case 'd': return this.checkKeyword('default', TokenType.TOKEN_DEFAULT);
            case 'e': {
                if (this.current - this.start > 1) {
                    const innerCharacter = this.source.substring(this.start + 1, this.start + 3); //check two characters
                    switch (innerCharacter) {
                        case 'ls':  return this.checkKeyword('else', TokenType.TOKEN_ELSE);
                        case 'xt':  return this.checkKeyword('extends', TokenType.TOKEN_EXTENDS);
                        case 'xp':  return this.checkKeyword('export', TokenType.TOKEN_EXPORT);
                    }
                }
            }
            case 'f': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.charAt(this.start + 1);
                    switch (innerCharacter){
                        case 'a': return this.checkKeyword('false', TokenType.TOKEN_FALSE);
                        case 'o': return this.checkKeyword('for', TokenType.TOKEN_FOR);
                        case 'u': return this.checkKeyword('fun', TokenType.TOKEN_FUN);
                        case 'r': return this.checkKeyword('from', TokenType.TOKEN_FROM);
                    }
                }
                break;
            }
            case 'i': {
                if (this.current - this.start > 1) {
                    const innerCharacter = this.source.charAt(this.start + 1);
                    switch (innerCharacter) {
                        case 'f': return this.checkKeyword('if', TokenType.TOKEN_IF);
                        case 'm':  return this.checkKeyword('import', TokenType.TOKEN_IMPORT);
                    }
                }
            }
            case 'n': return this.checkKeyword('nil', TokenType.TOKEN_NIL);
            case 'o': return this.checkKeyword('or', TokenType.TOKEN_OR);
            case 'p': return this.checkKeyword('print', TokenType.TOKEN_PRINT);
            case 'r': return this.checkKeyword('return', TokenType.TOKEN_RETURN);
            case 's': {
                if (this.current - this.start > 1) {
                    const innerCharacter = this.source.charAt(this.start + 1);
                    switch (innerCharacter) {
                        case 'u': return this.checkKeyword('super', TokenType.TOKEN_SUPER);
                        case 'w':  return this.checkKeyword('switch', TokenType.TOKEN_SWITCH);
                    }
                }
            }
            case 't': {
                if (this.current - this.start > 1){
                    const innerCharacter = this.source.charAt(this.start + 1);
                    switch (innerCharacter){
                        case 'h': return this.checkKeyword('this', TokenType.TOKEN_THIS);
                        case 'r': return this.checkKeyword('true', TokenType.TOKEN_TRUE);
                    }
                }
                break;
            }
            case 'v': return this.checkKeyword('var', TokenType.TOKEN_VAR);
            case 'w': return this.checkKeyword('while', TokenType.TOKEN_WHILE);
        }
        return TokenType.TOKEN_IDENTIFIER;
    };

    string() {
        while (this.peek() !== '"' && !this.isAtEnd()){
            if (this.peek() === '\n'){
                this.line++;
            }
            this.advance();
        }

        if (this.isAtEnd()){
            return this.errorToken("Unterminated string.");
        }



        //at this point, the scanner already consume the first quote
        //therefore, we need to account for the two quotes.
        const firstQuote = 1;
        const tokenString = {
            type: TokenType.TOKEN_STRING,
            length: this.current - this.start - firstQuote,
            line: this.line,
            payload: this.source.substring(this.start + firstQuote, this.current)
        }

        // The closing quote
        this.advance();

        return tokenString;
    };

    isDigit (character) {
        const reg = new RegExp(/^\d/);
        return reg.test(character);
    };

    number(){
        while (this.isDigit(this.peek())){
            this.advance();
        }

        if (this.peek() === '.' && this.isDigit(this.peekNext())){
            //consume the ".".
            this.advance();
            while(this.isDigit(this.peek())){
                this.advance();
            }
        }
        // the actual number is returned as a number
        const tokenNumber = {
            type: TokenType.TOKEN_NUMBER,
            // start: Number(scanner.source.substring(scanner.start, scanner.current)),
            length: this.current - this.start,
            line: this.line,
            payload: this.source.substring(this.start, this.current),
        };
        return tokenNumber;
    };

    isAlpha (character) {
        // const reg = new RegExp(/^[_a-zA-Z]/);
        const vietReg = new RegExp(/^[a-zA-Z_]/);
        return vietReg.test(character);
    };


    identifier () {
        while(this.isAlpha(this.peek()) || this.isDigit(this.peek())){
            this.advance();//keep going until the identifier ends
        }
        const payload = this.source.substring(this.start, this.current);
        // return this.makeToken(this.identifierType(), payload);
        return this.makeToken(this.vietIdentifierType(), payload);
    };


    scanToken() {
        this.skipWhiteSpace();
        this.start = this.current;

        if (this.isAtEnd()) return this.makeToken(TokenType.TOKEN_EOF);

        const character = this.advance();
        if (this.isAlpha(character)) return this.identifier();
        if (this.isDigit(character)) {
            return this.number();
        }
        switch(character){
            case '(': return this.makeToken(TokenType.TOKEN_LEFT_PAREN, '(');
            case ')': return this.makeToken(TokenType.TOKEN_RIGHT_PAREN, ')');
            case '{': return this.makeToken(TokenType.TOKEN_LEFT_BRACE, '{');
            case '}': return this.makeToken(TokenType.TOKEN_RIGHT_BRACE, '}');
            case ';': return this.makeToken(TokenType.TOKEN_SEMICOLON, ';');
            case ',': return this.makeToken(TokenType.TOKEN_COMMA, ',');
            case '.': return this.makeToken(TokenType.TOKEN_DOT, '.');
            case '/': return this.makeToken(TokenType.TOKEN_SLASH, '/');
            case '*': return this.makeToken(TokenType.TOKEN_STAR, '*');
            case ':': return this.makeToken(TokenType.TOKEN_COLON, ':');
            case '[': return this.makeToken(TokenType.TOKEN_LEFT_BRACKET, '[');
            case ']': return this.makeToken(TokenType.TOKEN_RIGHT_BRACKET, ']');
            /* one or two characters together*/
            case '!': {
                if (this.match('=')){
                    return this.makeToken(TokenType.TOKEN_BANG_EQUAL, '!=');
                } else {
                    return this.makeToken( TokenType.TOKEN_BANG, '!');
                }
            }
            case '=': {
                if(this.match('=')){
                    return this.makeToken( TokenType.TOKEN_EQUAL_EQUAL, '==');
                } else {
                    return this.makeToken(TokenType.TOKEN_EQUAL, '=');
                }
            }
            case '<': {
                if (this.match('=')){
                    return this.makeToken( TokenType.TOKEN_LESS_EQUAL, '<=');
                } else {
                    return this.makeToken(TokenType.TOKEN_LESS, '<');
                }
            }
            case '>': {
                if (this.match('=')) {
                    return this.makeToken(TokenType.TOKEN_GREATER_EQUAL, '>=');
                } else {
                    return this.makeToken(TokenType.TOKEN_GREATER, '>');
                }
            }
            case '+': {
                if (this.match('+')){
                    return this.makeToken( TokenType.TOKEN_INCREMENT, '++');
                } else {
                    return this.makeToken( TokenType.TOKEN_PLUS, '+');
                }
            }
            case '-': {
                if (this.match('-')){
                    return this.makeToken( TokenType.TOKEN_DECREMENT, '--');
                } else {
                    return this.makeToken(TokenType.TOKEN_MINUS, '-');
                }
            }
            case '"': {
                return this.string();
            }
            default:
                return this.errorToken("Unexpected character.");
        }
    };

}