import Scanner from "../../1scanner.js";
import TokenType, {keywords} from "../Types/TokenType.js";

import print from "../../print.js";
import ParserAnalysis from "./ParserAnalysis.js";

class Parser {
    current = null;
    previous = null;
    hadError = false;
    panicMode = false;
    scanner = null;
    allTokens = []; //debugging purposes
    //for continue and break statement
    insideLoop = false;
    loopStart = -1; //for continue statement only
    //for breaks statement
    innerMostFlowForBreak = -1; // for especially break statement within loop
    detectBreak = false; //for break statement
    insideSwitchCase = false; //for break statement


    constructor() {
        this.resetParser();
    }

    setSource(source){
        this.resetParser();
        this.scanner.setSource(source);
        return this;
    }

    //movement
    advance(){
        this.previous = this.current;

        for (;;) {
            this.current = this.scanner.scanToken();
            this.allTokens.push(this.current);
            if (this.current.type !== TokenType.TOKEN_ERROR) break;

            this.errorAtCurrent(this.current.payload);
        }
    };

    consume(type, message){
        if (this.current.type === type) {
            this.advance();
            return;
        }
        this.errorAtCurrent(message);
    };

    check(type){
        return this.current.type === type;
    };

    match(type) {
        if (!this.check(type)) return false;
        this.advance();
        return true;
    };

    //errors handling
    errorAt(token, message){
        if (this.panicMode) return;

        this.panicMode = true;
        let outputError = `[line ${token.line}] Error`;

        if (token.type === TokenType.TOKEN_EOF){
            outputError += " at end";
        } else if (token.type === TokenType.TOKEN_ERROR){
            //Do nothing
        } else {
            outputError += ` at '${token.payload}' `;
        }

        outputError += `: ${message}`;
        print(outputError);
        this.hadError = true;
    }

    errorAtCurrent(message){
        this.errorAt(this.current, message);
    };

    error(message){
        this.errorAt(this.previous, message);
    }

    resetLoopVariables(){
        this.insideLoop = false;
        this.loopStart = -1;
    }

    resetParser(){
        this.scanner = new Scanner();
        this.current = null;
        this.previous = null;
        this.hadError = false;
        this.panicMode = false;
        this.allTokens = []; //debugging purposes
        //for continue and break statement
        this.insideLoop = false;
        this.loopStart = -1; //for continue statement only
        //for breaks statement
        this.innerMostFlowForBreak = -1; // for especially break statement within loop
        this.detectBreak = false; //for break statement
        this.insideSwitchCase = false; //for break statement
    }

    addAllTokensLineBreak(){
        const newLineToken = {
            length: 2,
            line: -1,
            type: TokenType.TOKEN_LINE_BREAK,
            payload: "\n",
        };
        const last = this.allTokens.pop();
        this.allTokens = [...this.allTokens, newLineToken, last];
    }

    removeAllTokensLineBreak(){
        //remove the last two
        if (this.allTokens[this.allTokens.length - 2].type === TokenType.TOKEN_LINE_BREAK){
            const last = this.allTokens.pop();
            this.allTokens.pop();//remove line break;
            this.allTokens.push(last);
        }
    }

    addAllTokensSpace(){
        const newLineToken = {
            length: 1,
            line: -1,
            type: TokenType.TOKEN_SPACE,
            payload: " ",
        };
        const last = this.allTokens.pop();
        this.allTokens = [...this.allTokens, newLineToken, last];
    }

    addAllTokensSpaceBefore(){
        const newLineToken = {
            length: 1,
            line: -1,
            type: TokenType.TOKEN_SPACE,
            payload: " ",
        };
        const last = this.allTokens.pop();
        const secondLast = this.allTokens.pop();
        this.allTokens = [...this.allTokens, newLineToken, secondLast, last];
    }

    removeAllTokensSpace(){
        //remove the last two
        if (this.allTokens[this.allTokens.length - 2].type === TokenType.TOKEN_SPACE){
            const last = this.allTokens.pop();
            this.allTokens.pop();//remove line break;
            this.allTokens.push(last);
        }
    }

    addAllTokenTab(){
        const newLineToken = {
            length: 1,
            line: -1,
            type: TokenType.TOKEN_TAB,
            payload: "\t",
        };
        const last = this.allTokens.pop();
        this.allTokens = [...this.allTokens, newLineToken, last];
    }

    generateTokens(){
        const parserAnalysis = new ParserAnalysis(this);
        this.advance();
        while(!this.match(TokenType.TOKEN_EOF)){
            // this.advance();
            parserAnalysis.declarationAnalysis();
        }

        return this;
    }

    transformTokensValue(){

        this.allTokens = this.allTokens.map(token => {
            const found = keywords.find(key => key ===token.type);
            if (found){
                //token is keywords
                switch(token.payload){
                    case 'va':
                        return {...token, payload: 'v'}
                    case 'nghi':
                        return {...token, payload: 'ngh'}
                    case 'truong_hop':
                        return {...token, payload: 'trng_hp'}
                    case 'lop':
                        return {...token, payload: 'lp'}
                    case 'tiep':
                        return {...token, payload: 'tip'}
                    case 'khac':
                        return {...token, payload: 'khc'}
                    case 'theo':
                        return {...token, payload: 'theo'}
                    case 'xuat':
                        return {...token, payload: 'xut'}
                    case 'khiem_dien':
                        return {...token, payload: 'khim_din'}
                    case 'sai':
                        return {...token, payload: 'sai'}
                    case 'cho':
                        return {...token, payload: 'cho'}
                    case 'tu_day':
                        return {...token, payload: 't_y'}
                    case 'ham':
                        return {...token, payload: 'hm'}
                    case 'neu':
                        return {...token, payload: 'nu'}
                    case 'nhap':
                        return {...token, payload: 'nhp'}
                    case 'vo_gia':
                        return {...token, payload: 'v_gi'}
                    case 'hoac':
                        return {...token, payload: 'hoc'}
                    case 'viet':
                        return {...token, payload: 'vit'}
                    case 'tra':
                        return {...token, payload: 'tr'}
                    case 'goc':
                        return {...token, payload: 'gc'}
                    case 'doi':
                        return {...token, payload: 'i'}
                    case 'nay':
                        return {...token, payload: 'ny'}
                    case 'dung':
                        return {...token, payload: 'ng'}
                    case 'bien':
                        return {...token, payload: 'bin'}
                    case 'trong_khi':
                        return {...token, payload: 'trng_khi'}
                }
            } else if (token.payload === 'Mang'){
                //check for array
                return {...token, payload: "Mng"}
            }
            return token;

        })

        return this;
    }

    returnAllTokensAsText(){
        let results = this.allTokens.map(token => {
            if (token.type === TokenType.TOKEN_STRING){
                return `"${token.payload}"`;
            }
            return token.payload
        }).join("");
        return results;

    }
}

export default Parser;